.App {
  text-align: center;
  background-color: #222;
  padding-top: 0.25px;
  padding-left: 10px;
  padding-right: 10px;
  padding-bottom: 50px;
}

.header-logo img{
  margin-top: 10px;
  margin-left: 20%;
  height: 40px;
}

.App-footer {
  background-color: #222;
  height: 80px;
  padding-top: 50px;
  color: white;
}
.container {
  background-color: #f2f2f2;
  padding-bottom: 10px;
  padding-left: 10px;
  padding-right: 10px;
  border-radius: 6px;
}

.languageSelection p {
  text-align: right;
  margin-right: 50px;
  margin-top: 5px;
  padding-top: 5px;
}

.languageSelection p a {
  padding-left: 10px;
  padding-right: 10px;
  font-size: large;
}

.languageSelection p a:hover {
  background-color: lightgray;
  border-radius: 10;
}

.homestyle {
  background-color: #222;
  padding: 20px;
  color: white;
  border-radius: 4px;
}

.homestyle h2 {
  padding-bottom:20px;
}

.home-upperBlock {
  padding-top: 25px;
  font-size: large;
}

.home-lowerBlock {
  padding-top: 15px;
  padding-bottom: 25px;
}

.homestyle img {
  width: 120px;
  margin-top: 5px;
  margin-right:5px;
  border-radius: 2px;
}

.startBlock {
  margin-top: 20px;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 5px;
  padding-bottom: 5px;
}

.startBlock p {
  padding-top: 2px;
  padding-bottom: 2px;
  margin: auto;
  width: 100px;
  background-color: lightgray;
  border-radius: 4px;
}

.blackButton {
  color: #222;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 4px;
  padding-bottom: 4px;
}

.blackButton a {
  padding-left: 12px;
  padding-right: 12px;
  padding-top: 1.5px;
  padding-bottom: 1.5px;
  font-size: large;
}


.blueButton {
  color: #0080ff;
}

.resultbox {
  padding-top: 20px;
  padding-bottom: 10px;
  font-size: large;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



